import React, { Component, Fragment } from 'react'

export default class Prices extends Component {
  state = {
    currency: 'USD'
  }
  render() {
    const key = this.props.bpi.USD
    return (
      <Fragment>
        <ul className="list-group">
          <li className="list-group-item">
            Bitcoin rate for {key.description}: <span className="badge badge-primary">{key.code}</span> <strong>{key.rate}</strong>
          </li>
        </ul>
      </Fragment>
    )
  }
}
